.error {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.error__link {
  color: var(--color-grey);
  text-decoration: underline;
  transition: .2s;

  &:hover,
  &:focus-visible {
    color: var(--color-blue);
  }
}