.form__group {
  margin-bottom: 15px;

  &:last-child { margin-bottom: 0; }

  &--checkbox {
    margin-top: 15px;
    
    .form__label {
      font-weight: 400;
    }
  }
}

.form__input {
  display: block;
  width: 100%;
  padding: 10px 15px;
  background-color: var(--color-white);
  background-image: none;
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  box-sizing: border-box;

  &:focus {
    outline: 1px solid var(--color-blue);
    border-color: var(--color-blue);
  }
}

.form__label {
  display: inline-block;
  max-width: 100%;
  font-weight: 700;
  margin-bottom: 5px;
  cursor: pointer;

  &:last-child { margin-bottom: 0; }
}

.form__textarea {
  display: block;
  width: 100%;
  height: 120px;
  padding: 10px 15px;
  background: none;
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  box-sizing: border-box;
  resize: none;

  &::placeholder { opacity: 0; }
  &:focus {
    outline: 1px solid var(--color-blue);
    border-color: var(--color-blue);
  }
}

.form__submit {
  @include btn;
  &--sm { @include btn-sm; }
}