html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0 0 0.67em;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

:root {
  --color-black: #000;
  --color-white: #fff;
  --color-grey: #777;
  --color-dark-grey: #333;
  --color-blue: #235ef5;
  --color-blue-hover: #396ef6;
  --color-red: #ff4b55;
  --color-bg: #f1f1f1;
  --color-border: #ccc;
  --border-radius: 4px;
}

html {
  font-size: 16px;
  line-height: 1.4;
  font-weight: 400;
  height: 100%;
}

body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: var(--color-dark-grey);
  min-height: 100%;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
}

svg {
  fill: currentColor;
}

span[data-timestamp] {
  color: var(--color-grey);
  white-space: nowrap;
}

.overflow-hidden {
  overflow: hidden;
}

.header {
  background-color: var(--color-bg);
  border-bottom: 1px solid var(--color-border);
  position: relative;
}

.header__container {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  padding-top: 12px;
  padding-bottom: 12px;
}
@media (min-width: 768px) {
  .header__container {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1440px) {
  .header__container {
    padding-left: 50px;
    padding-right: 50px;
  }
}

.header__logo {
  font-size: 1.125rem;
  line-height: 1;
  margin-right: 15px;
  z-index: 99;
}
@media (min-width: 768px) {
  .header__logo {
    font-size: 1.3rem;
  }
}
@media (min-width: 992px) {
  .header__logo {
    margin-right: 30px;
  }
}

.header__search {
  width: 100%;
  position: relative;
  z-index: 99;
}

.header__search-input {
  color: var(--color-dark-grey);
  width: 100%;
  padding: 5px 35px 5px 15px;
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  box-sizing: border-box;
  transition: 0.2s;
}
.header__search-input:focus {
  outline: 1px solid var(--color-blue);
  border-color: var(--color-blue);
}

.header__search-submit {
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-grey);
  width: 35px;
  height: 100%;
  padding: 5px;
  background-color: transparent;
  border: none;
  border-radius: var(--border-radius);
  box-sizing: border-box;
  cursor: pointer;
  transition: 0.2s;
  position: absolute;
  top: 0;
  right: 0;
}
.header__search-submit:hover, .header__search-submit:focus-visible {
  color: var(--color-dark-grey);
}

.header__notification {
  z-index: 99;
}

.nav {
  display: flex;
  align-items: center;
  gap: 0 15px;
  width: 100%;
}
@media (max-width: 767px) {
  .nav--active .nav__toggle-item {
    height: 0;
  }
  .nav--active .nav__toggle-item::before {
    transform: translate(0) rotate(45deg);
  }
  .nav--active .nav__toggle-item::after {
    transform: translate(0) rotate(-45deg);
  }
  .nav--active .nav__mobile {
    display: flex;
  }
}
@media (min-width: 992px) {
  .nav {
    gap: 0 30px;
  }
}

.nav__header {
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .nav__header {
    margin-right: 10px;
  }
}

.nav__toggle {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: var(--color-grey);
  width: 24px;
  height: 24px;
  margin-left: auto;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: 0.2s;
  z-index: 99;
  order: 3;
}
.nav__toggle:hover, .nav__toggle:focus-visible {
  color: var(--color-dark-grey);
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}

.nav__toggle-item {
  width: 20px;
  height: 2px;
  background-color: currentColor;
  position: relative;
}
.nav__toggle-item::before, .nav__toggle-item::after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: inherit;
  position: absolute;
  top: 0;
  left: 0;
  transition: transform 0.2s;
}
.nav__toggle-item::before {
  transform: translateY(-6px);
}
.nav__toggle-item::after {
  transform: translateY(6px);
}

.nav__list {
  display: none;
  list-style: none;
  margin: 0;
  padding-left: 0;
}
@media (min-width: 768px) {
  .nav__list {
    display: flex;
    align-items: center;
  }
}

.nav__item {
  margin-right: 15px;
}
.nav__item:last-child {
  margin-right: 0;
}
@media (min-width: 992px) {
  .nav__item {
    margin-right: 30px;
  }
}

.nav__link {
  display: block;
  color: var(--color-grey);
  white-space: nowrap;
  transition: 0.2s;
}
.nav__link:hover, .nav__link:focus-visible {
  color: var(--color-dark-grey);
}

.nav__mobile {
  display: none;
  width: 100vw;
  height: calc(100vh - 59px);
  background-color: var(--color-bg);
  box-sizing: border-box;
  overflow: auto;
  position: absolute;
  top: 59px;
  left: 0;
  animation: 0.2s ease-in show-menu;
  z-index: 98;
}

.nav__mobile-wrap {
  display: flex;
  flex-direction: column;
  margin: auto;
  padding: 30px;
}

.nav__mobile-link {
  color: var(--color-dark-grey);
  font-size: 1.5rem;
  line-height: 1;
  padding: 5px;
  transition: 0.2s;
}
.nav__mobile-link:not(:last-child) {
  margin-bottom: 10px;
}
.nav__mobile-link:hover, .nav__mobile-link:focus-visible {
  color: var(--color-blue);
}

@keyframes show-menu {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}
.notification {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: var(--color-grey);
  padding: 5px;
  border-radius: var(--border-radius);
  position: relative;
  transition: 0.2s;
}
.notification:hover, .notification:focus-visible {
  color: var(--color-dark-grey);
}
.notification--active {
  color: var(--color-white);
  background-color: var(--color-blue);
}
.notification--active:hover, .notification--active:focus-visible {
  color: var(--color-white);
  background-color: var(--color-blue-hover);
}

.notification__badge {
  display: inline-block;
  padding: 0.35em 0.65em;
  font-size: 0.75em;
  font-weight: 700;
  line-height: 1;
  color: #fff;
  text-align: center;
  white-space: nowrap;
  background-color: #dc3545;
  border-radius: 50rem;
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 100%;
  transform: translate(-50%, -50%);
}

.main__container {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  box-sizing: border-box;
  height: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
}
@media (min-width: 768px) {
  .main__container {
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1440px) {
  .main__container {
    padding-left: 50px;
    padding-right: 50px;
  }
}

@media (max-width: 767px) {
  .main__title {
    text-align: center;
  }
}

.auth {
  width: 100%;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}

.error {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.error__link {
  color: var(--color-grey);
  text-decoration: underline;
  transition: 0.2s;
}
.error__link:hover, .error__link:focus-visible {
  color: var(--color-blue);
}

.login__help {
  margin-top: 15px;
}

.login__help-text:last-child {
  margin-bottom: 0;
}
.login__help-text > a {
  color: var(--color-grey);
  text-decoration: underline;
  transition: 0.2s;
}
.login__help-text > a:hover {
  color: var(--color-blue);
}

.alert {
  color: #004085;
  margin-bottom: 30px;
  padding: 15px;
  background-color: #cce5ff;
  border-color: #b8daff;
}
.alert--info {
  color: #0c5460;
  background-color: #d1ecf1;
  border-color: #bee5eb;
}
.alert--success {
  color: #155724;
  background-color: #d4edda;
  border-color: #c3e6cb;
}
.alert--warning {
  color: #856404;
  background-color: #fff3cd;
  border-color: #ffeeba;
}
.alert--danger {
  color: #721c24;
  background-color: #f8d7da;
  border-color: #f5c6cb;
}

.form__group {
  margin-bottom: 15px;
}
.form__group:last-child {
  margin-bottom: 0;
}
.form__group--checkbox {
  margin-top: 15px;
}
.form__group--checkbox .form__label {
  font-weight: 400;
}

.form__input {
  display: block;
  width: 100%;
  padding: 10px 15px;
  background-color: var(--color-white);
  background-image: none;
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  box-sizing: border-box;
}
.form__input:focus {
  outline: 1px solid var(--color-blue);
  border-color: var(--color-blue);
}

.form__label {
  display: inline-block;
  max-width: 100%;
  font-weight: 700;
  margin-bottom: 5px;
  cursor: pointer;
}
.form__label:last-child {
  margin-bottom: 0;
}

.form__textarea {
  display: block;
  width: 100%;
  height: 120px;
  padding: 10px 15px;
  background: none;
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  box-sizing: border-box;
  resize: none;
}
.form__textarea::placeholder {
  opacity: 0;
}
.form__textarea:focus {
  outline: 1px solid var(--color-blue);
  border-color: var(--color-blue);
}

.form__submit {
  color: var(--color-white);
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  width: 100%;
  padding: 12px 16px;
  background-color: var(--color-blue);
  border: none;
  border-radius: var(--border-radius);
  box-sizing: border-box;
  cursor: pointer;
  transition: 0.2s;
}
.form__submit:hover, .form__submit:focus-visible {
  background-color: var(--color-blue-hover);
}
.form__submit--sm {
  color: var(--color-white);
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  width: 100%;
  padding: 12px 16px;
  background-color: var(--color-blue);
  border: none;
  border-radius: var(--border-radius);
  box-sizing: border-box;
  cursor: pointer;
  transition: 0.2s;
  font-size: 0.75rem;
  width: auto;
  padding: 8px 14px;
}
.form__submit--sm:hover, .form__submit--sm:focus-visible {
  background-color: var(--color-blue-hover);
}

.tooltip {
  display: none;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
}
.tooltip[data-show] {
  display: block;
}

.post {
  display: grid;
  align-items: start;
  gap: 20px;
  padding-top: 15px;
  border-top: 1px solid var(--color-border);
}
.post:not(:last-child) {
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .post {
    grid-template-columns: 70px 1fr;
  }
}

.post__avatar {
  width: 70px;
  height: 70px;
  object-fit: cover;
}

.post__top {
  margin-bottom: 5px;
}

.post__author {
  color: var(--color-blue);
}

.post__text {
  font-size: 1rem;
  line-height: 1.5;
  word-wrap: break-word;
  max-width: 1250px;
}
.post__text:last-child {
  margin-bottom: 0;
}
@media (max-width: 767px) {
  .post__text {
    width: calc(100vw - 30px);
  }
}
@media (min-width: 768px) {
  .post__text {
    width: calc(100vw - 150px);
  }
}
@media (min-width: 1440px) {
  .post__text {
    width: auto;
  }
}

.post__translate {
  display: inline-block;
  padding: 5px 10px;
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  transition: 0.2s;
}
.post__translate:hover, .post__translate:focus-visible {
  border-color: var(--color-dark-grey);
}

.pagination {
  margin-top: 30px;
}
@media (min-width: 768px) {
  .pagination {
    margin-top: 50px;
  }
}

.pagination__list {
  display: flex;
  gap: 10px;
  align-items: center;
  list-style: none;
  margin: 0;
  padding-left: 0;
}

.pagination__prev {
  margin-right: auto;
}

.pagination__next {
  margin-left: auto;
}

.pagination__prev .pagination__link,
.pagination__next .pagination__link {
  padding: 5px 10px;
}

@media (max-width: 767px) {
  .pagination__item {
    display: none;
  }
}

.pagination__link {
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
  min-width: 30px;
  min-height: 25px;
  border: 1px solid var(--color-border);
  border-radius: 4px;
  box-sizing: border-box;
  transition: 0.2s;
}
.pagination__link:hover, .pagination__link:focus-visible {
  border-color: var(--color-blue);
}
.pagination__link--active {
  color: var(--color-white);
  background-color: var(--color-blue);
  border-color: var(--color-blue);
}
.pagination__link--active:hover, .pagination__link--active:focus-visible {
  color: var(--color-white);
  background-color: var(--color-blue-hover);
}
.pagination__link--disabled {
  opacity: 0.6;
  pointer-events: none;
}
@media (min-width: 992px) {
  .pagination__link {
    min-width: 35px;
    min-height: 30px;
  }
}

@media (max-width: 767px) {
  .pagination__ellipsis {
    display: none;
  }
}

.user {
  display: grid;
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .user {
    grid-template-columns: 256px 1fr;
    gap: 30px;
    margin-bottom: 50px;
  }
}

@media (max-width: 767px) {
  .user__avatar {
    margin: 0 auto 30px;
  }
}

.user__description {
  align-self: center;
}

.user__btns {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px 15px;
  max-width: 700px;
}
@media (max-width: 767px) {
  .user__btns {
    grid-template-columns: 1fr;
    max-width: 100%;
  }
}

.user__btn {
  color: var(--color-white);
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  width: 100%;
  padding: 12px 16px;
  background-color: var(--color-blue);
  border: none;
  border-radius: var(--border-radius);
  box-sizing: border-box;
  cursor: pointer;
  transition: 0.2s;
}
.user__btn:hover, .user__btn:focus-visible {
  background-color: var(--color-blue-hover);
}

.user-popup {
  display: grid;
  grid-template-columns: 64px 1fr;
  gap: 10px;
  color: var(--color-dark-grey);
  padding: 15px;
}
@media (max-width: 767px) {
  .user-popup {
    max-width: 300px;
  }
}

.user-popup__username {
  color: var(--color-blue);
  margin-bottom: 5px;
}

.user-popup__text {
  margin-bottom: 5px;
}
.user-popup__text:last-child {
  margin-bottom: 0;
}

.user-popup__follow-info {
  white-space: nowrap;
}

.footer {
  padding-top: 15px;
  padding-bottom: 15px;
  text-align: center;
  background-color: var(--color-bg);
  border-top: 1px solid var(--color-border);
  font-size: 18px;
  line-height: 1;
}